import React, { useState, useEffect } from 'react';
import axios from 'axios';

const FileShare = () => {
  const [file, setFile] = useState(null);
  const [files, setFiles] = useState([]);

  useEffect(() => {
    // Fetch uploaded files
    axios.get(`${process.env.REACT_APP_API_URL}/files`).catch(() => setFiles([]));
  }, []);

  const handleUpload = async () => {
    const formData = new FormData();
    formData.append('file', file);
    try {
      const res = await axios.post(`${process.env.REACT_APP_API_URL}/upload`, formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      setFiles(prev => [...prev, { filename: res.data.filename, original_name: file.name }]);
      setFile(null);
    } catch (err) {
      alert('Upload failed');
    }
  };

  return (
    <div className="mt-10">
      <h2 className="text-2xl font-bold">File Sharing</h2>
      <div className="mt-4">
        <input
          type="file"
          onChange={e => setFile(e.target.files[0])}
          className="p-2 border rounded"
          aria-label="File upload input"
        />
        <button
          onClick={handleUpload}
          className="ml-2 bg-primary text-white p-2 rounded"
          disabled={!file}
        >
          Upload
        </button>
      </div>
      <ul className="mt-4 space-y-2">
        {files.map((f, i) => (
          <li key={i} className="p-2 border rounded">
            {f.original_name}
            <a
              href={`${process.env.REACT_APP_API_URL}/download/${f.filename}`}
              className="ml-2 text-primary"
              download
            >
              Download
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default FileShare;