import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const Dashboard = () => {
  const [matches, setMatches] = useState([]);
  const [progress, setProgress] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) navigate('/');
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;

    axios.post(`${process.env.REACT_APP_API_URL}/match`).then(res => setMatches(res.data.matches));
    axios.get(`${process.env.REACT_APP_API_URL}/progress`).catch(() => setProgress([{ sessions: 3, badge: 'Beginner' }]));
  }, [navigate]);

  const joinRoom = (partnerId) => {
    const roomId = `room-${Math.random().toString(36).substr(2, 9)}`;
    navigate(`/room/${roomId}`);
    axios.post(`${process.env.REACT_APP_API_URL}/progress`, { partner_id: partnerId, progress: { sessions: 1 } });
  };

  return (
    <div className="mt-10">
      <h2 className="text-2xl font-bold">Dashboard</h2>
      <p className="text-secondary">Based on research: 89% students want AI matching!</p>
      <h3 className="mt-4">AI Matches</h3>
      <ul className="space-y-2">
        {matches.map((match, i) => (
          <li key={i} className="p-2 border rounded">
            {match.email} (Compatibility: {match.compatibility * 100}%)
            <button onClick={() => joinRoom(match.id)} className="ml-4 bg-primary text-white p-1 rounded">Join Study Room</button>
          </li>
        ))}
      </ul>
      <h3 className="mt-4">Progress</h3>
      <ul className="space-y-2">
        {progress.map((p, i) => (
          <li key={i} className="p-2 border rounded">Sessions: {p.sessions}, Badge: {p.badge}</li>
        ))}
      </ul>
    </div>
  );
};

export default Dashboard;