import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const Register = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    age: '',
    academic_level: '',
    field_of_study: '',
    learning_style: '',
    schedule: { monday: '', tuesday: '', wednesday: '', thursday: '', friday: '' }
  });
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name.startsWith('schedule.')) {
      const day = name.split('.')[1];
      setFormData({ ...formData, schedule: { ...formData.schedule, [day]: value } });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post(`${process.env.REACT_APP_API_URL}/register`, formData);
      navigate('/');
    } catch (err) {
      alert('Registration failed');
    }
  };

  return (
    <div className="max-w-md mx-auto mt-10">
      <form onSubmit={handleSubmit} className="space-y-4">
        <h2 className="text-2xl font-bold">Register</h2>
        <input name="email" onChange={handleChange} placeholder="Email" className="w-full p-2 border rounded" required aria-label="Email" />
        <input name="password" type="password" onChange={handleChange} placeholder="Password" className="w-full p-2 border rounded" required aria-label="Password" />
        <input name="age" type="number" onChange={handleChange} placeholder="Age" className="w-full p-2 border rounded" aria-label="Age" />
        <select name="academic_level" onChange={handleChange} className="w-full p-2 border rounded" aria-label="Academic Level">
          <option value="">Select Academic Level</option>
          <option value="Freshman">Freshman</option>
          <option value="Sophomore">Sophomore</option>
          <option value="Junior">Junior</option>
          <option value="Senior">Senior</option>
          <option value="Graduate">Graduate</option>
        </select>
        <input name="field_of_study" onChange={handleChange} placeholder="Field of Study" className="w-full p-2 border rounded" aria-label="Field of Study" />
        <input name="learning_style" onChange={handleChange} placeholder="Learning Style (e.g., Visual)" className="w-full p-2 border rounded" aria-label="Learning Style" />
        <div className="grid grid-cols-2 gap-2">
          <input name="schedule.monday" onChange={handleChange} placeholder="Monday (e.g., 9-12)" className="p-2 border rounded" aria-label="Monday Schedule" />
          <input name="schedule.tuesday" onChange={handleChange} placeholder="Tuesday (e.g., 9-12)" className="p-2 border rounded" aria-label="Tuesday Schedule" />
          <input name="schedule.wednesday" onChange={handleChange} placeholder="Wednesday (e.g., 9-12)" className="p-2 border rounded" aria-label="Wednesday Schedule" />
          <input name="schedule.thursday" onChange={handleChange} placeholder="Thursday (e.g., 9-12)" className="p-2 border rounded" aria-label="Thursday Schedule" />
          <input name="schedule.friday" onChange={handleChange} placeholder="Friday (e.g., 9-12)" className="p-2 border rounded" aria-label="Friday Schedule" />
        </div>
        <button type="submit" className="bg-primary text-white p-2 rounded w-full">Register</button>
      </form>
    </div>
  );
};

export default Register;