import React, { useEffect } from 'react';
import Chart from 'chart.js/auto';

const Stats = () => {
  const demographics = [
    { category: '18-20 years', count: 45, percentage: '35.4%' },
    { category: '21-23 years', count: 52, percentage: '40.9%' },
    { category: '24-26 years', count: 23, percentage: '18.1%' },
    { category: '27+ years', count: 7, percentage: '5.5%' }
  ];

  const fields = [
    { category: 'Technical', count: 67, percentage: '52.8%' },
    { category: 'Business', count: 23, percentage: '18.1%' },
    { category: 'Liberal Arts', count: 21, percentage: '16.5%' },
    { category: 'Social Sciences', count: 16, percentage: '12.6%' }
  ];

  useEffect(() => {
    new Chart(document.getElementById('wtpChart'), {
      type: 'bar',
      data: {
        labels: ['Mean', 'Median'],
        datasets: [{ label: 'Willingness to Pay ($)', data: [15.40, 14.00], backgroundColor: '#1D4ED8' }]
      },
      options: { scales: { y: { beginAtZero: true } } }
    });

    new Chart(document.getElementById('isolationChart'), {
      type: 'pie',
      data: {
        labels: ['Isolation (73%)', 'No Isolation (27%)'],
        datasets: [{ data: [73, 27], backgroundColor: ['#1D4ED8', '#6B7280'] }]
      }
    });
  }, []);

  return (
    <div className="mt-10" aria-label="Research Statistics Page">
      <h2 className="text-2xl font-bold">Research Statistics</h2>
      <p className="text-secondary">Data from 127 survey respondents, 5 interviews, 18 focus group participants (April-Sep 2024).</p>
      <h3 className="mt-4">Demographics</h3>
      <table className="w-full border-collapse">
        <thead>
          <tr className="bg-primary text-white">
            <th className="p-2">Category</th>
            <th className="p-2">Count</th>
            <th className="p-2">%</th>
          </tr>
        </thead>
        <tbody>
          {demographics.map(d => (
            <tr key={d.category} className="border">
              <td className="p-2">{d.category}</td>
              <td className="p-2">{d.count}</td>
              <td className="p-2">{d.percentage}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <h3 className="mt-4">Fields of Study</h3>
      <table className="w-full border-collapse">
        <thead>
          <tr className="bg-primary text-white">
            <th className="p-2">Category</th>
            <th className="p-2">Count</th>
            <th className="p-2">%</th>
          </tr>
        </thead>
        <tbody>
          {fields.map(f => (
            <tr key={f.category} className="border">
              <td className="p-2">{f.category}</td>
              <td className="p-2">{f.count}</td>
              <td className="p-2">{f.percentage}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <h3 className="mt-4">Willingness to Pay</h3>
      <canvas id="wtpChart" className="max-w-md" aria-label="Willingness to Pay Chart"></canvas>
      <h3 className="mt-4">Isolation Statistics</h3>
      <canvas id="isolationChart" className="max-w-md" aria-label="Isolation Statistics Chart"></canvas>
      <p className="mt-4">Key Findings: 73% report isolation, 89% interested in AI matching, 68% spend {'>'}2h/week coordinating (p&lt;0.001).</p>
    </div>
  );
};

export default Stats;