import React, { useEffect, useState } from 'react';
import io from 'socket.io-client';
import { useParams } from 'react-router-dom';

const socket = io('http://localhost:3001', { transports: ['websocket'] });

const StudyRoom = () => {
  const { roomId } = useParams();
  const [message, setMessage] = useState('');
  const [chat, setChat] = useState([]);

  useEffect(() => {
    socket.emit('join-room', roomId);
    socket.on('chat-message', (msg) => setChat(prev => [...prev, msg]));
    socket.on('user-joined', (id) => setChat(prev => [...prev, `User ${id} joined`]));

    return () => {
      socket.off('chat-message');
      socket.off('user-joined');
    };
  }, [roomId]);

  const sendMessage = () => {
    socket.emit('chat-message', roomId, message);
    setChat(prev => [...prev, message]);
    setMessage('');
  };

  return (
    <div className="mt-10">
      <h2 className="text-2xl font-bold">Study Room {roomId}</h2>
      <div className="mt-4">
        <h3>Chat</h3>
        <ul className="border p-4 h-64 overflow-y-auto">
          {chat.map((m, i) => <li key={i} className="mb-2">{m}</li>)}
        </ul>
        <div className="mt-2 flex">
          <input
            value={message}
            onChange={e => setMessage(e.target.value)}
            className="flex-1 p-2 border rounded"
            aria-label="Chat message input"
          />
          <button onClick={sendMessage} className="ml-2 bg-primary text-white p-2 rounded">Send</button>
        </div>
        <div className="mt-4">Video Chat Placeholder (WebRTC in production)</div>
        <button
          onClick={() => socket.emit('share-screen', roomId, 'screen data')}
          className="mt-2 bg-primary text-white p-2 rounded"
        >
          Share Screen
        </button>
      </div>
    </div>
  );
};

export default StudyRoom;